.book-card-body {
    margin:auto;
    background: #1F4963 !important;
    width: 30vw;
    align-items: center;
}

.book-card-image {
    width: 15vw !important; 
    padding-top: 20px;
}

.book-info {
    color: #FDE9C9;
}

/* .review-text {
    display: none !important;
} */
.button {
	width: 100%;
	background-color: #fee9c9;
	color: #02897f;
}

.contact {
	width: 90%;
	margin: auto;
	text-align: left;
}

.mb-3 {
	color: #fee9c9;
}


@media (min-width: 1024px) {
	.contact {
		width: 50%;

		text-align: left;
	}
}

